*{
  margin: 0px;
}

.header{
  display: flex;
  align-items: center;
  color: white;
  width: 100% ;
  height: 10vh;
  background-color: black;
}

.header_title{
  font-size: 35px;
  margin-left: 35px;
}

.list{
  border-bottom: 1px solid black;
  padding: 15px;
}

.list h3{
  display: inline;
}

.list button{
  margin-left: 10px;
}

.modal{
  margin: 0px auto;
  width: 50%;
  height: 40vh;
  background-color: rgb(200, 190, 190);
}

.modal h2, .modal p{
  font-size: 24px;
  padding: 20px;
  padding-right: 10px;
}

.modal button{
  margin-left: 20px;
}
